.container{
    height: 6rem;
    width: 100%;
    background: rgb(36, 138, 113);
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    font-size: 2.2rem;
    font-family: 'Nunito', sans-serif;
    font-weight: 800;
    line-height: 3rem;
    letter-spacing: 3px;
}
.cities{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 2.2rem;
    font-family: 'Nunito', sans-serif;
    text-transform: uppercase;
    font-weight: 800;
    line-height: 3rem;
    letter-spacing: 3px;
}
.selectedCity{
    padding: .3rem 1rem .3rem 1rem;
    font-size: 1.5rem;
    margin-right: 3rem;
    font-weight: 700;
    color: black;
    border-radius: 1rem;
    background: white;
}